.c-sid-default__left-col {
  width: 30%;
  margin-right: 15px;
  justify-content: center;
}
.c-sid-default__left-col img {
  max-height: 250px;
}
.c-sid-default__right-col {
  overflow: hidden;
}
.c-sid-default__dragarea {
  font-weight: bold;
  text-align: center;
  padding: 9% 0;
  color: #ccc;
  border: 2px dashed #ccc;
  border-radius: 7px;
  cursor: default;
}
.c-sid-default__dragarea.hover {
  color: #333;
  border-color: #333;
  background-color: #f9f9f9;
}
.c-sid-default__img-loader {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 180px;
}
.c-sid-default__size-info {
  margin-top: 5px;
  font-size: 13px;
}
.c-sid-default__remove {
  margin-left: 5px;
}
.c-sid-default__modal .btn {
  position: relative;
}

.c-sid-modal .modal-body {
  position: relative;
}
.c-sid-modal__content {
  position: relative;
  z-index: 3;
}
.c-sid-modal__loading {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
}
.is-invalid[uni-field-validate] [data-field-error] {
  display: block;
}